.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 480px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  border: 3px solid rgb(159 170 189);
  border-radius: 8px;
  box-shadow: 12px -7px 4px 1px #9cbfba;
  background-color: #e7f1f9;
}
.title {
  margin: 20px 20px;
  font-family: cursive;
  color: #647ecf;
}
